@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: rgb(97, 218, 251);
}
body{
  background: rgb(40, 44, 52);
  height: 100vh;
  width: 100vw;
  overflow-y: hidden;
  overflow-x: hidden;
}
.App{
  text-align: center;
}
.Navbar{
  height: 50px;
}
.Navbar button{
  /* padding: 15px; */
  padding: 0 15px;
  height: 100%;
  font-size: 15px;
  color: black;
}
.home{
  font-size: 50px;

}
.wrapImageImage{
  height: calc(100vh - 50px);
  width: 100%;
}
.wrapImage{
  height: fit-content;
  height: 100%;
  position: relative;
  margin: 0 auto;
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* border: 1px solid rgb(144, 250, 58); */
}
.wrapImageSecond{
  height: fit-content;
  height: 100%;
  position: relative;
  margin: 0 auto;
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapImage1{
  animation: 
  leftRight1  5s 5 linear, 
  topBottom1 5s 25s 5 linear forwards;
}
.wrapImage2 {
  animation: spin2 3s 10 linear,
  spin2reverse 3s 30s 10 linear;
}
.wrapImage3{
  animation: 
  leftRight3  10s 5 linear;
}
.wrapImage4{
  animation: 
  closeAndOpen4  0.7s 43 linear;
}
.wrapImage5{
  animation: 
  closeAndOpen5 8s 5 linear;
}
.wrapImage6{
  animation: 
  leftRight1 6s 10 linear;
  width: 30%;
}
.wrapImage2 > img {
  /* animation: spin2 3s infinite linear; */
  /* animation: spin-reverse2 3s infinite linear; */
}

.wrapImage img{
  /* width: 73px; */
  /* height: 50px; */
  width: 100%;
  max-width: 150px;
  position: absolute;
  margin: 0 auto;
  /* color: rgb(255, 251, 0); */
}

.wrapImageSecond img{
  width: 100%;
  /* height: 100%; */
  position: absolute;
  margin: 0 auto;
}

.wrapImage7{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.wrapImage71{
  height: fit-content;
  height: 100%;
  position: relative;
  margin: 0 auto;
  width: 20%;
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: closeAndOpen7 2s 10 linear;
}
.wrapImage72{
  height: fit-content;
  height: 100%;
  position: relative;
  margin: 0 auto;
  width: 20%;
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: closeAndOpen7 2s 10 1s linear;
}


/* 7 */
@keyframes closeAndOpen7{
  from {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  to {
    opacity: 0;
    
  }
  }

/* 1 */
@keyframes leftRight1 {
  from {left: 0px;}
  /* 25% {left: calc(50% - 55px)} */
  25% {left: calc(50% - 5%)}
  75% {left: calc(-50% + 5%)}
  to {left: 0px;}
  }
@keyframes topBottom1 {
  from {top: 0px;}
  25% {top: calc(50% - 5%)}
  75% {top: calc(-50% + 5%)}
  to {top: 0px;}
  }


/* 2 */
@keyframes spin2 {
  from {
    transform: 
      rotate(0turn)
      translateY(-30%)
      rotate(1turn)
  }
  to { 
    transform: 
      rotate(1turn)
      translateY(-30%)
      rotate(0turn)
  }
}
@keyframes spin2reverse {
  from {
    transform: 
      rotate(1turn)
      translateY(-30%)
      rotate(0turn)
  }
  to { 
    transform: 
      rotate(0turn)
      translateY(-30%)
      rotate(1turn)
  }
}

/* 3 */
@keyframes leftRight3 {
  from {
    left: calc(-50% + 5%);
    top: calc(-50% + 5%);
  }
  30% {
    left: calc(50% - 5%);
    top: calc(50% - 5%);
  }
  50% {
    left: calc(50% - 5%);
    top: calc(-50% + 5%);
  }
  80% {
    left: calc(-50% + 5%);
    top: calc(50% - 5%);
  }
  to {
    left: calc(-50% + 5%);
    top: calc(-50% + 5%);
  }
  }
/* 4 */
@keyframes closeAndOpen4 {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
    
  }
  }
@keyframes closeAndOpen5 {
  from {
    opacity: 1;
  }
  45% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  95% {
    opacity: 0;
  }
  to {
    opacity: 1;
    
  }
  }
